(function() {
    'use strict';

    angular
        .module('oncoscape')
        .directive('osPlsr', explore);

    /** @ngInject */
    function explore() {

        var directive = {
            restrict: 'E',
            templateUrl: 'app/components/plsr/plsr.html',
            controller: PlsrController,
            controllerAs: 'vm',
            bindToController: true
        };

        return directive;

        /** @ngInject */
        function PlsrController(osApi, osHistory, $state, $stateParams, $timeout, $scope, d3, moment, $sce, $window, _) {

            if (angular.isUndefined($stateParams.datasource)) {
                $state.go("datasource");
                return;
            }

            // Elements
            var d3Chart = d3.select("#plsr-chart").append("svg").attr("id", "chart");
            var d3Tooltip = d3.select("body").append("div").attr("class", "tooltip plsr-tooltip")

            // Properties
            var width, height, xScale, yScale, xMax, yMax, brush;

            // View Model
            var vm = this;
            vm.datasource = $stateParams.datasource;
            vm.deathMinFilter = vm.deathMinValue = 45;
            vm.deathMaxFilter = vm.deathMaxValue = 66;
            vm.survivalMinFilter = vm.survivalMinValue = 3;
            vm.survivalMaxFilter = vm.survivalMaxValue = 7;
            vm.geneSets = [];
            vm.geneSet = null;
            vm.frame;
            vm.tip = null;

            // History Integration
            var selectedIds = (osHistory.getGeneSelection() == null) ? [] : osHistory.getGeneSelection().ids;

            function saveSelected() {
                var selected = d3Chart.selectAll(".plsr-node-selected")[0];
                if (selected.length == 0) return;
                osHistory.addGeneSelection("PLSR", "Manual Selection",
                    d3Chart.selectAll(".plsr-node-selected")[0].map(function(node) {
                        return node.__data__.name.toUpperCase()
                    })
                );
            }

            function setSelected() {
                if (selectedIds.length == 0) {
                    d3Chart.selectAll(".plsr-node-selected").classed("plsr-node-selected", false);
                } else {
                    d3Chart.selectAll("circle").classed("plsr-node-selected", function() {
                        return (selectedIds.indexOf(this.__data__.name) >= 0)
                    });
                }
            }

            // Initialize
            osApi.setBusy(true)("Loading Dataset");
            osApi.setDataset(vm.datasource).then(function(response) {
                var mtx = response.payload.rownames.filter(function(v) {
                    return v.indexOf("mtx.mrna") >= 0
                });
                mtx = mtx[mtx.length - 1].replace(".RData", "");
                osApi.getPLSR(vm.datasource, mtx).then(function() {
                    osApi.getGeneSetNames().then(function(response) {

                        // Load Gene Sets
                        vm.geneSets = response.payload;
                        vm.geneSet = vm.geneSets[0];
                        osApi.getSummarizedPLSRPatientAttributes().then(function(response) {

                            // Load Min Max Values
                            var payload = response.payload;
                            vm.deathMinValue = Math.floor(payload.AgeDx[0] / 365.24);
                            vm.deathMaxValue = Math.floor(payload.AgeDx[4] / 365.24);
                            vm.survivalMinValue = Math.floor(payload.Survival[0] / 365.24);
                            vm.survivalMaxValue = Math.floor(payload.Survival[4] / 365.24);
                            $scope.$watch('vm.geneSet', function() {
                                vm.update();
                            });

                            // History
                            osHistory.onGeneSelectionChange.add(function(selection) {
                                selectedIds = selection.ids;
                                setSelected();
                            });
                        });
                    });
                });
            });

            // API Call To Calculate PLSR
            vm.update = function() {

                var factors = [{
                    name: "Survival",
                    low: Number(vm.survivalMinFilter) * 365.24,
                    high: Number(vm.survivalMaxFilter) * 365.24
                }, {
                    name: "AgeDx",
                    low: Number(vm.deathMinFilter) * 365.24,
                    high: Number(vm.deathMaxFilter) * 365.24
                }];

                osApi.getCalculatedPLSR(vm.geneSet, factors).then(function(response) {

                    // Clean Up Data
                    var payload = response.payload;
                    var genes = payload.loadings.map(function(item, index) {
                        item.name = this[index];
                        return item;
                    }, payload.loadingNames);
                    var vectors = payload.vectors.map(function(item, index) {
                        item.name = this[index];
                        return item;
                    }, payload.vectorNames);
                    var abs = payload.maxValue * 1.2;
                    draw(abs, vectors, genes);
                    osApi.setBusy(false);
                });

                function setScale() {
                    width = $window.innerWidth - 400;
                    if (angular.element(".tray-right").attr("locked") == "false") {
                        width += 300;
                    }
                    height = $window.innerHeight - 190;
                    if (angular.element(".tray").attr("locked") == "true") width -= 300;

                    d3Chart
                        .attr("width", '100%')
                        .attr("height", height);
                    xScale = d3.scale.linear()
                        .domain([-xMax, xMax])
                        .range([0, width]).nice();

                    yScale = d3.scale.linear()
                        .domain([-yMax, yMax])
                        .range([height, 0]).nice();
                }

                // Render
                function draw(abs, vectors, genes) {

                    xMax = abs * .9;
                    yMax = abs * .9;
                    setScale();

                    // Brush
                    brush = d3.svg.brush()
                        .x(xScale)
                        .y(yScale)
                        .on("brushend", function() {
                            var bv = brush.extent();
                            d3Chart.selectAll("circle")
                                .classed("plsr-node-selected", function(d) {
                                    return (d[0] > bv[0][0] && d[0] < bv[1][0] && d[1] > bv[0][1] && d[1] < bv[1][1]);
                                });
                            d3.select(this).transition().duration(300)
                                .call(brush.extent([
                                    [0, 0],
                                    [0, 0]
                                ]));
                            saveSelected();
                        });

                    d3Chart.call(brush);

                    // Circles
                    var circles = d3Chart.selectAll("circle").data(genes, function(d) {
                        return d;
                    });
                    circles.enter()
                        .append("circle")
                        .attr({
                            "class": "plsr-node",
                            "cx": width * .5,
                            "cy": height * .5,
                            "r": 3
                        })
                        .style("fill-opacity", "0")
                        .on("mouseover", function(d) {
                            d3Tooltip.transition()
                                .duration(200)
                                .style("opacity", 1);
                            d3Tooltip.html(d.name)
                                .style("left", (d3.event.pageX + 15) + "px")
                                .style("top", (d3.event.pageY - 15) + "px");
                        })
                        .on("mouseout", function() {
                            d3Tooltip.transition()
                                .duration(500)
                                .style("opacity", 0);
                        })
                        .on("click", function(d) {
                            angular.element('#plsr-webpage').modal();
                            var url = "https://www.genecards.org/cgi-bin/carddisp.pl?gene=" + d.name;
                            $scope.$apply(function() {
                                vm.frame = $sce.trustAsResourceUrl(url);
                            });
                        });
                    circles.transition()
                        .duration(750)
                        .delay(function(d, i) {
                            return i / 300 * 500;
                        })
                        .attr("cx", function(d) {
                            return xScale(d[0]);
                        })
                        .attr("cy", function(d) {
                            return yScale(d[1]);
                        })
                        .style("fill-opacity", 1);

                    circles.exit()
                        .transition()
                        .duration(600)
                        .delay(function(d, i) {
                            return i / 300 * 500;
                        })
                        .attr("cx", width * .5)
                        .attr("cy", height * .5)
                        .style("fill-opacity", "0")
                        .remove();

                    // Lines
                    var lines = d3Chart.selectAll("line").data(vectors)
                    lines.enter()
                        .append("line")
                        .attr({
                            "class": "line",
                            "stroke-width": 3,
                            "x1": xScale(0),
                            "y1": yScale(0),
                            "x2": xScale(0),
                            "y2": yScale(0)
                        })
                        .style("stroke", function(d) {
                            return (d.name.indexOf("Age")) ? "#1396de" : "#38347b"
                        });
                    lines.transition()
                        .duration(900)
                        .attr({
                            "x1": xScale(0),
                            "y1": yScale(0),
                            "x2": function(v) {
                                return xScale(v[0]);
                            },
                            "y2": function(v) {
                                return yScale(v[1]);
                            }
                        });
                    lines.exit().remove();

                    // Text
                    var text = d3Chart.selectAll("text").data(vectors);
                    text.enter()
                        .append("text")
                        .attr({
                            "class": "text",
                            "x": width * .5,
                            "y": height * .5,
                            "text-anchor": function(v) {
                                return (v[0] > 0) ? "start" : "end"
                            }
                        })
                        .text(function(v) {
                            return v.name;
                        })
                        .style({
                            "fill": "black",
                            "text-anchor": "middle"
                        });

                    text.transition()
                        .duration(900)
                        .attr({
                            "x": function(v) {
                                return xScale(v[0]);
                            },
                            "y": function(v) {
                                return yScale(v[1]);
                            }
                        });

                    text.exit().remove();
                    setSelected();
                }

                vm.resize = function() {
                    setScale();

                    d3Chart.selectAll("circle")
                        .attr({
                            "cx": function(d) {
                                return xScale(d[0]);
                            },
                            "cy": function(d) {
                                return yScale(d[1]);
                            }
                        });

                    d3Chart.selectAll("text")
                        .attr({
                            "x": function(v) {
                                return xScale(v[0]);
                            },
                            "y": function(v) {
                                return yScale(v[1]);
                            }
                        });

                    d3Chart.selectAll("line")
                        .attr({
                            "x1": xScale(0),
                            "y1": yScale(0),
                            "x2": function(v) {
                                return xScale(v[0]);
                            },
                            "y2": function(v) {
                                return yScale(v[1]);
                            }
                        });
                };

                // Listen For Resize
                angular.element($window).bind('resize',
                    _.debounce(vm.resize, 300)
                );
            };

        }
    }
})();

(function() {
    'use strict';

    angular
        .module('oncoscape')
        .directive('osPca', explore);

    /** @ngInject */
    function explore() {

        var directive = {
            restrict: 'E',
            templateUrl: 'app/components/pca/pca.html',
            controller: PcaController,
            controllerAs: 'vm',
            bindToController: true
        };

        return directive;

        /** @ngInject */
        function PcaController($q, osApi, osCohortService, $state, $stateParams, $timeout, $scope, d3, moment, $window, _) {

            // Retrieve Selected Patient Ids From OS Service
            var pc = osCohortService.getCohort();
            if (pc === null) {
                osCohortService.setCohort([], "All Patients", osCohortService.SAMPLE);
            }

            var selectedIds = (pc === null) ? [] : pc.sampleIds;
            var onCohortChange = function(cohort) {
                if (osCohortServiceUpdate) {
                    selectedIds = cohort.patientsIds;
                    setSelected();
                } else {
                    osCohortServiceUpdate = true;
                }
            };

            var osCohortServiceUpdate = true;
            osCohortService.onCohortChange.add(onCohortChange);

            function setSelected() {
                if (selectedIds.length === 0) {
                    d3Points.selectAll(".pca-node-selected").classed("pca-node-selected", false);
                } else {
                    d3Points.selectAll("circle").classed("pca-node-selected", function() {
                        return (selectedIds.indexOf(this.__data__.id) >= 0);
                    });
                }
            }

            // Elements
            var d3Chart = d3.select("#pca-chart").append("svg");
            var d3Points = d3Chart.append("g");
            var d3xAxis = d3Chart.append("g");
            var d3yAxis = d3Chart.append("g");
            var d3Brush = d3Chart.append("g");

            // Properties
            var scaleX, scaleY, axisX, axisY;
            var data, minMax;
            var width, height;

            var colors = {
                data: [],
                dataset: osApi.getDataSource().disease,
                name: "None",
                type: "color"
            };

            // View Model
            var vm = (function(vm, osApi) {
                vm.datasource = osApi.getDataSource();
                vm.geneSets = [];
                vm.geneSet = null;
                vm.search = "";
                vm.selectColor = function(e) {
                    var ids = e.values;
                    var allIds = [];
                    d3.selectAll("circle").each(function(d) {
                        if (ids.indexOf(d.id) != -1) {
                            d3.select(this).classed("pca-node-selected", true);
                            allIds.push(d.id);
                        } else {
                            if (d3.select(this).classed("pca-node-selected")) allIds.push(d.id);
                        }
                    });
                    osCohortService.setCohort(allIds, "PCA", osCohortService.SAMPLE);
                };
                vm.deselectColor = function(e) {
                    var ids = e.values;
                    var allIds = [];
                    d3.selectAll("circle").each(function(d) {
                        if (ids.indexOf(d.id) != -1) {
                            d3.select(this).classed("pca-node-selected", false);
                        } else {
                            if (d3.select(this).classed("pca-node-selected")) allIds.push(d.id);
                        }
                    });
                    osCohortService.setCohort(allIds, "PCA", osCohortService.SAMPLE);
                };
                osApi.query("render_pca", {
                        disease: vm.datasource.disease,
                        $fields: ['type', 'geneset', 'source']
                    })
                    .then(function(response) {
                        var data = response.data.map(function(v) {
                            return {
                                a: v.geneset,
                                b: v.source,
                                c: v.type
                            };
                        });

                        var result = _.reduce(data, function(memo, val) {
                            var tmp = memo;
                            _.each(val, function(fldr) {
                                if (!_.has(tmp, fldr)) {
                                    tmp[fldr] = {};
                                }
                                tmp = tmp[fldr];
                            });
                            return memo;
                        }, {});
                        vm.geneSets = Object.keys(result).map(function(geneset) {
                            return {
                                name: geneset,
                                sources: Object.keys(result[geneset]).map(function(source) {
                                    return {
                                        name: source,
                                        types: Object.keys(result[geneset][source]).map(function(type) {
                                            return {
                                                name: type
                                            };
                                        })
                                    };
                                })
                            };
                        });


                        vm.geneSet = vm.geneSets[0];
                    });
                return vm;

            })(this, osApi);

            // Updates PCA Types When Geneset Changes
            $scope.$watch('vm.geneSet', function() {
                if (vm.geneSet === null) return;
                vm.sources = vm.geneSet.sources;
                vm.source = vm.sources[0];
            });
            $scope.$watch('vm.source', function() {
                if (vm.geneSet === null) return;
                vm.pcaTypes = vm.source.types;
                vm.pcaType = vm.pcaTypes[0];
            });

            // Fetches PCA Data + Calculates Min Max for XYZ
            $scope.$watch('vm.pcaType', function(geneset) {
                if (angular.isUndefined(geneset)) return;

                osApi.query("render_pca", {
                        disease: vm.datasource.disease,
                        geneset: vm.geneSet.name,
                        type: vm.pcaType.name,
                        source: vm.source.name
                    })
                    .then(function(response) {
                        vm.pc1 = response.data[0].pc1;
                        vm.pc2 = response.data[0].pc2;
                        var keys = Object.keys(response.data[0].data);
                        data = keys.map(function(key) {
                            this.data[key].id = key;
                            return this.data[key];
                        }, {
                            data: response.data[0].data
                        });
                        minMax = data.reduce(function(p, c) {
                            p.xMin = Math.min(p.xMin, c[0]);
                            p.xMax = Math.max(p.xMax, c[0]);
                            p.yMin = Math.min(p.yMin, c[1]);
                            p.yMax = Math.max(p.yMax, c[1]);
                            return p;
                        }, {
                            xMin: Infinity,
                            yMin: Infinity,
                            xMax: -Infinity,
                            yMax: -Infinity
                        });

                        minMax.xMax = Math.max(Math.abs(minMax.xMin), minMax.xMax);
                        minMax.xMin = -minMax.xMax;
                        minMax.yMax = Math.max(Math.abs(minMax.yMin), minMax.yMax);
                        minMax.yMin = -minMax.yMax;

                        draw();
                    });
            });

            function setColors() {

                // Set Legend
                vm.legendCaption = colors.name;
                vm.legendNodes = colors.data;

                // If No Color Specified
                if (colors.name == "None") {
                    vm.legendCaption = "";
                    data.forEach(function(v) {
                        v.color = '#0096d5';
                    });

                    // Color Based On V
                } else {
                    var degMap = colors.data.reduce(function(p, c) {
                        for (var i = 0; i < c.values.length; i++) {
                            p[c.values[i]] = c.color;
                        }
                        return p;
                    }, {});
                    data = data.map(function(v) {
                        v.color = (angular.isDefined(this[v.id])) ? this[v.id] : "#DDD";
                        return v;
                    }, degMap);
                }
            }

            function draw() {

                // Colorize
                setColors();

                // Size
                var layout = osApi.getLayout();
                width = $window.innerWidth - layout.left - layout.right;
                height = $window.innerHeight - 120; //10
                angular.element("#pca-chart").css({
                    "width": width + "px",
                    "padding-left": layout.left + "px"
                });
                d3Chart.attr("width", width).attr("height", height);
                d3Brush.attr("width", width).attr("height", height);
                d3Points.attr("width", width).attr("height", height);

                // Scale
                scaleX = d3.scaleLinear().domain([minMax.xMin, minMax.xMax]).range([50, width - 50]).nice();
                scaleY = d3.scaleLinear().domain([minMax.yMin, minMax.yMax]).range([50, height - 50]).nice();

                // Draw
                var circles = d3Points.selectAll("circle").data(data);
                circles.enter().append("svg:circle")
                    .attr("class", "pca-node")
                    .attr("cx", function(d) {
                        return scaleX(d[0]);
                    })
                    .attr("cy", function(d) {
                        return scaleY(d[1]);
                    })
                    .attr("r", 3)
                    .style("fill", function(d) {
                        return d.color;
                    });
                circles.exit()
                    .transition()
                    .duration(200)
                    .delay(function(d, i) {
                        return i / 300 * 100;
                    })
                    .style("fill-opacity", "0")
                    .remove();
                circles
                    .style("fill", function(d) {
                        return d.color;
                    })
                    .transition()
                    .duration(750)
                    .delay(function(d, i) {
                        return i / 300 * 100;
                    })
                    .attr("r", 3)
                    .attr("cx", function(d) {
                        return scaleX(d[0]);
                    })
                    .attr("cy", function(d) {
                        return scaleY(d[1]);
                    })
                    .style("fill", function(d) {
                        return d.color;
                    })
                    .style("fill-opacity", 0.8);

                // Axis
                axisX = d3.axisTop().scale(scaleX).ticks(5);
                axisY = d3.axisLeft().scale(scaleY).ticks(5);

                d3xAxis
                    .attr("class", "axis")
                    .attr("transform", "translate(0, " + scaleY(0) + ")")
                    .call(axisX)
                    .append("text")
                    .attr("x", 50)
                    .attr("y", 15)
                    .text("PC1");

                d3yAxis
                    .attr("class", "axis")
                    .attr("transform", "translate(" + scaleX(0) + ", 0)")
                    .call(axisY)
                    .append("text")
                    .attr("y", 55)
                    .attr("x", 25)
                    .text("PC2");

                // Brush
                var brush = d3.brush()
                    .on("end", function() {

                        if (!d3.event.selection) {
                            osCohortService.setCohort([], "PCA", osCohortService.SAMPLE);
                            return;
                        }

                        var bv = d3.event.selection;
                        var xMin = bv[0][0];
                        var xMax = bv[1][0];
                        var yMin = bv[0][1];
                        var yMax = bv[1][1];

                        var ids = d3Points.selectAll("circle").data().filter(function(d) {
                            var x = scaleX(d[0]);
                            var y = scaleY(d[1]);
                            return (x > xMin && x < xMax && y > yMin && y < yMax);
                        }).map(function(d) {
                            return d.id;
                        });
                        debugger;
                        osCohortService.setCohort(ids, "PCA", osCohortService.SAMPLE);

                    });

                d3Brush.attr("class", "brush").call(brush);

                setSelected();

            }

            // Listen For Resize
            osApi.onResize.add(draw);
            angular.element($window).bind('resize', _.debounce(draw, 300));

            var onPatientColorChange = function(value) {
                colors = value;
                vm.showPanelColor = false;
                draw();
            };

            osCohortService.onPatientColorChange.add(onPatientColorChange);

            // Destroy
            $scope.$on('$destroy', function() {
                osCohortService.onPatientColorChange.remove(onPatientColorChange);
                osCohortService.onCohortChange.remove(onCohortChange);
            });
        }
    }
})();
